@font-face {
  font-family: "Blogger Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2"), url("../fonts/bloggersans-bolditalic.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2"), url("../fonts/bloggersans-medium.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans.woff2") format("woff2"), url("../fonts/bloggersans.woff") format("woff");
}
@font-face {
  font-family: "Blogger Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/bloggersans-light.woff2") format("woff2"), url("../fonts/bloggersans-light.woff2") format("woff");
}
img {
  height: auto;
  max-width: 100%;
  object-fit: contain;
  display: block;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input,
textarea {
  outline: none;
}

input:active,
textarea:active {
  outline: none;
}

input:focus,
textarea:focus {
  outline: none;
}

textarea {
  resize: none;
  overflow: hidden;
}

input {
  all: unset;
  appearance: none;
}

.input[type] {
  display: inline-block;
  appearance: none;
  box-sizing: border-box;
}

.alphabet {
  display: none;
}

.alphabet--open {
  display: block;
}

.alphabet__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.alphabet__item {
  background-color: transparent;
  margin-bottom: 3.9px;
}

.alphabet__link {
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #454545;
  text-decoration: none;
  width: 100%;
  height: auto;
  display: block;
  outline: none;
}
.alphabet__link:hover {
  color: #1d2e5d;
}
.alphabet__link:focus-visible, .alphabet__link:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .alphabet__item {
    margin-bottom: 10px;
  }

  .alphabet__item--country {
    margin: 0;
  }

  .alphabet__link {
    font-size: 20px;
    line-height: 1;
  }
}
@media (min-width: 1440px) {
  .alphabet {
    display: block;
    margin-bottom: 39px;
  }

  .alphabet__link {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
  }

  .alphabet__item {
    margin-bottom: 0;
    text-align: left;
  }
}
