.add-plan {
  width: 84%;
  min-width: 272px;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  color: #454545;
  margin: 0 auto;
  padding-bottom: 63px;
  box-sizing: border-box;
}

.add-plan__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  margin-bottom: 31px;
}

.add-plan__title {
  margin: 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  color: #1d2e5d;
}

.add-plan__button-group-header {
  margin-right: 18px;
  margin-top: 3px;
  display: flex;
}

.add-plan__button-header {
  border: none;
  background-color: rgba(29, 46, 93, 0.3);
  width: 6px;
  height: 6px;
  border-radius: 50%;
  padding: 0;
  margin-left: 15px;
}

.add-plan__button-desc {
  display: none;
  color: rgba(0, 0, 0, 0.6);
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.add-plan__button-step {
  position: relative;
  padding: 0;
  border: none;
}

.add-plan__button-header--current {
  background-color: #1d2e5d;
}

.add-plan__small {
  font-weight: 500;
  line-height: 16px;
  color: rgba(29, 46, 93, 0.3);
}

.add-plan__input {
  font-weight: 500;
  font-style: 14.86px;
  line-height: 14px;
}

.add-plan__form-step-first {
  row-gap: 20px;
  padding-left: 2px;
  padding-right: 3px;
}

.add-plan__step {
  margin-bottom: 9px;
  width: 100%;
  padding-top: 22px;
  box-sizing: border-box;
  border-radius: 10px;
}
.add-plan__step:last-of-type {
  margin-bottom: 0;
}

.add-plan__step--second {
  padding-top: 20px;
}

.add-plan__subtitle {
  margin: 0;
  margin-bottom: 17px;
  margin-left: 2px;
}

.add-plan__subtitle--second {
  margin: 0;
  margin-bottom: 16px;
}

.add-plan__text {
  margin: 0;
  margin-bottom: 21px;
  font-size: 16px;
  line-height: 22px;
}

.add-plan__text--second {
  margin-bottom: 22px;
}

.add-plan__text p {
  margin: 0;
}

.add-plan__text br {
  display: none;
}

.add-plan__text--first br {
  display: inline-block;
}

.add-plan__form {
  width: 100%;
}

.add-plan__table {
  margin-bottom: 20px;
}

.add-plan__button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-weight: 700;
  width: 97%;
  min-width: 220px;
  border-radius: 23px;
  border: none;
  font-size: 17px;
  line-height: 20px;
  padding-top: 10px;
  padding-bottom: 9px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  text-transform: uppercase;
  background-color: #ffd84d;
  cursor: pointer;
  color: #1d2e5d;
  outline: none;
}
.add-plan__button:not(:last-of-type) {
  margin-bottom: 5px;
}

.container-step__third .add-plan__button {
  min-width: 231px;
}

.add-plan__button-icon {
  position: absolute;
  bottom: 0;
  top: 0;
  margin-top: auto;
  margin-bottom: auto;
  right: 30px;
  display: none;
  width: 13px;
  height: auto;
}

.add-plan__button-back {
  background-color: transparent;
  min-width: auto;
  outline: none;
}
.add-plan__button-back:hover {
  background-color: transparent;
  opacity: 0.6;
}
.add-plan__button-back:active, .add-plan__button-back:focus-visible {
  opacity: 0.3;
}

.add-plan__button-back:hover .add-plan__button-icon {
  opacity: 0.6;
}

.add-plan__button-back:active .add-plan__button-icon,
.add-plan__button-back:focus .add-plan__button-icon {
  opacity: 0.3;
}

.add-plan__step--third .add-plan__button-back {
  padding-top: 12px;
}

.add-plan__button-wrapper {
  margin-right: 3px;
}

@media (min-width: 768px) {
  .add-plan {
    width: 80%;
    min-width: 619px;
    padding-bottom: 65px;
  }

  .add-plan__form-step-first {
    row-gap: 0;
  }

  .add-plan__subtitle {
    margin-left: 0;
    margin-bottom: 36px;
  }

  .add-plan__subtitle--second {
    margin-bottom: 35px;
  }

  .add-plan__header {
    margin-bottom: 58px;
  }

  .add-plan__title {
    font-size: 36px;
  }

  .add-plan__button-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .add-plan__button-wrapper--second {
    margin-right: 7px;
  }
  .add-plan__button-wrapper--second .add-plan__button-back {
    padding-right: 35px;
  }

  .add-plan__button-group-header {
    margin: 0;
  }

  .add-plan__button-header {
    margin-left: 28px;
  }

  .add-plan__step {
    border-radius: 32px;
    padding-top: 45px;
    padding-bottom: 56px;
  }

  .add-plan__text {
    position: relative;
    font-size: 20px;
    line-height: 24px;
    padding-bottom: 40px;
    margin: 0;
  }
  .add-plan__text::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    right: 44px;
    height: 1px;
    background-color: #e2e4e9;
  }

  .add-plan__text br {
    display: inline-block;
  }

  .add-plan__text--second {
    margin-bottom: 0;
  }

  .add-plan__form {
    display: flex;
    flex-direction: column;
  }

  .add-plan__table {
    margin-bottom: 48px;
  }

  .add-plan__button {
    width: auto;
    min-width: 285px;
    min-height: 80px;
    font-size: 24px;
    line-height: 1;
    font-weight: 700;
    text-transform: none;
    padding-right: 35px;
    padding-left: 34px;
    padding-top: 10px;
    padding-bottom: 12px;
    margin-left: 0;
    border-radius: 22px;
    box-sizing: border-box;
    justify-content: space-between;
    flex-grow: 1;
  }

  .add-plan__button:not(:last-of-type) {
    margin-bottom: 0;
  }

  .add-plan__button-icon {
    display: block;
    fill: #1d2e5d;
    width: 12px;
    min-width: 12px;
    right: 36px;
  }

  .add-plan__step--third .add-plan__button {
    min-width: 286px;
  }

  .add-plan__step--third .add-plan__button-back {
    padding-left: 65px;
    padding-right: 2px;
    margin-right: 0;
    justify-content: flex-end;
    min-width: 250px;
  }

  .add-plan__step--third .add-plan__button-icon {
    margin: 0;
    margin-top: auto;
    margin-bottom: auto;
  }
  .add-plan__step--third .add-plan__button-icon--back {
    padding-bottom: 4px;
  }

  .add-plan__text--third {
    padding-bottom: 35px;
    margin-bottom: 26px;
  }

  .add-plan__desc-button-back {
    padding-left: 23px;
    margin-left: auto;
  }

  .add-plan__desc-button {
    padding-right: 15px;
  }

  .add-plan__button-back {
    min-width: auto;
    padding: 0;
    padding-left: 64px;
    padding-top: 15px;
    padding-bottom: 16px;
    padding-right: 4px;
  }

  .add-plan__button-back .add-plan__button-icon {
    transform: rotate(180deg);
    margin: 0;
    margin-top: auto;
    margin-bottom: auto;
    position: static;
    order: -1;
  }
}
@media (min-width: 1440px) {
  .add-plan {
    width: 91%;
    min-width: 1321px;
    padding-bottom: 0;
  }

  .add-plan__title {
    margin-left: 69px;
  }

  .add-plan__step {
    position: relative;
    padding-top: 98px;
    padding-bottom: 106px;
    margin-bottom: 43px;
    display: grid;
    grid-template-columns: 1fr 230px;
    grid-template-rows: min-content 32px min-content 55px min-content 32px min-content 57px min-content;
    border-radius: 61px;
  }

  .add-plan__step--second {
    padding-top: 95px;
    grid-template-rows: min-content 36px min-content 55px min-content 58px min-content;
  }
  .add-plan__step--second::before {
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background-color: #e2e4e9;
    top: 0;
    bottom: 0;
    right: -4px;
    grid-row: 5/-1;
    grid-column: 1/2;
    z-index: 2;
  }

  .add-plan__step--third {
    grid-template-rows: min-content 33px min-content 55px min-content 59px min-content;
  }

  .add-plan__step--third .add-plan__button-icon--back {
    padding: 0;
    left: 53px;
  }

  .add-plan__step--third .add-plan__button-back {
    padding-top: 28px;
    padding-right: 5px;
  }

  .add-plan__button-group-header {
    display: none;
  }

  .add-plan__button-group {
    display: flex;
    flex-direction: column;
    grid-column: 2/3;
    grid-row: 1/4;
    margin-top: 7px;
  }

  .add-plan__button-step {
    background-color: transparent;
    border: none;
    text-align: left;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.11em;
    text-transform: uppercase;
    font-family: "Blogger Sans", sans-serif, "Arial";
    margin-bottom: 20px;
  }

  .add-plan__button-step:last-of-type {
    margin-bottom: 0;
  }

  .add-plan__button {
    max-width: 450px;
    grid-column: 1/2;
    grid-row: -2/-1;
    min-width: 286px;
    flex-grow: 0;
    padding-top: 26px;
    padding-right: 50px;
    padding-left: 33px;
    padding-bottom: 27px;
    text-align: left;
    margin-left: 2px;
  }

  .add-plan__button-desc {
    position: relative;
    display: block;
    padding-left: 42px;
  }
  .add-plan__button-desc::after {
    content: "";
    position: absolute;
    top: calc(50% - 3px);
    left: 0;
    width: 6px;
    height: 6px;
    background-color: rgba(29, 46, 93, 0.3);
    border-radius: 50%;
  }

  .add-plan__desc-button-back {
    margin-left: 0;
    padding-left: 21px;
  }

  .add-plan__button-step--current .add-plan__button-desc {
    color: black;
  }
  .add-plan__button-step--current .add-plan__button-desc::after {
    background-color: #1d2e5d;
  }

  .add-plan__subtitle {
    grid-column: 1/2;
    grid-row: 1/2;
    margin-bottom: 0;
  }

  .add-plan__subtitle--second {
    margin-bottom: 0;
  }

  .add-plan__subtitle br {
    display: none;
  }

  .add-plan__text {
    padding: 0;
    grid-row: 3/4;
    grid-column: 1/2;
    padding-left: 2px;
    box-sizing: border-box;
  }
  .add-plan__text::after {
    display: none;
  }

  .add-plan__text--third {
    margin-bottom: 0;
  }

  .add-plan__form {
    grid-column: 1/3;
    grid-row: 5/6;
  }
  .add-plan__form::after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    content: "";
    height: 1px;
    background-color: #e2e4e9;
  }

  .add-plan__form-step-first {
    padding-top: 28px;
    row-gap: 14px;
  }

  .add-plan__table {
    grid-column: 1/3;
    grid-row: 7/8;
    margin-bottom: 0;
  }

  .add-plan__button-icon {
    right: 36px;
    margin-top: auto;
    margin-bottom: auto;
  }

  .add-plan__button-back {
    margin-right: 0;
    margin-left: auto;
    justify-content: flex-end;
  }

  .add-plan__button-wrapper {
    grid-column: 1/-1;
    grid-row: 7/8;
  }

  .add-plan__button-wrapper--second {
    margin: 0;
  }

  .add-plan__button-wrapper--second .add-plan__button-back {
    padding-right: 5px;
  }

  .add-plan__button--inactive {
    padding-right: 4px;
    padding-left: 77px;
    text-align: right;
  }

  .add-plan__step--third .add-plan__button-wrapper {
    margin-right: 0;
  }
}
.button {
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-weight: 700;
  color: #1d2e5d;
  height: fit-content;
  box-sizing: border-box;
  border: 1px solid transparent;
  cursor: pointer;
  background-color: #ffd84d;
  outline: none;
}
.button:hover {
  background-color: #ffbd2e;
  outline: none;
}
.button:active, .button:focus-visible {
  background-color: #ffbd2e;
  color: rgba(29, 46, 93, 0.3);
}

button:active .button__icon,
button:focus-visible .button__icon {
  opacity: 0.3;
}

.business-block {
  position: absolute;
  top: -11px;
  right: 0;
  left: 0;
  min-width: 270px;
  width: 84%;
  background-color: white;
  box-shadow: 0 2px 8px 0 rgba(29, 46, 93, 0.14);
  border-radius: 20px;
  padding-top: 32px;
  margin-left: auto;
  margin-right: auto;
  z-index: 100;
}

.business-block__content {
  width: 81%;
  min-width: 223px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 35px;
  text-align: center;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  color: #454545;
}

.business-block__title {
  font-size: 24px;
  line-height: 1;
  color: #ff8c2e;
  margin: 0;
  margin-bottom: 29px;
  padding-left: 17px;
  padding-right: 20px;
}

.business-block__list {
  position: relative;
  margin: 0;
  text-align: left;
}
.business-block__list::after {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.05);
  content: "";
  left: 0;
}

.business-block__term {
  position: relative;
  padding-top: 11px;
  padding-bottom: 0;
  padding-right: 20px;
}
.business-block__term::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.05);
  content: "";
}

.business-block__desc {
  margin: 0;
  padding-bottom: 10px;
  padding-right: 20px;
  margin-bottom: 1px;
}
.business-block__desc:last-of-type {
  margin-bottom: 0;
}

.business-block__button {
  width: 100%;
  border: none;
  background-color: #f2f3f7;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-weight: 700;
  line-height: 24px;
  font-size: 16px;
  color: #1d2e5d;
  text-transform: uppercase;
  padding-top: 20px;
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 18px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  cursor: pointer;
  outline: none;
}
.business-block__button:hover {
  background-color: #eef0f6;
