.form-step {
  position: relative;
  font-family: "Blogger Sans", "Arial", sans-serif;
  color: hsla(224, 52%, 24%, 1);
  margin-bottom: 23px;
  box-sizing: border-box;
}

.form-step__field-group {
  position: relative;
  display: grid;
  grid-template-columns: auto min-content;

  &:first-of-type {
    margin-bottom: 19px;
  }
}

.form-step__field-group--duration {
  margin-bottom: 20px;
}

.form-step__label {
  font-size: 16px;
  line-height: 1;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  font-weight: 500;
}

.form-step__label-desc {
  font-size: 15px;
  line-height: 1;
  margin-top: 2px;
}

.form-step__field-wrapper {
  position: relative;
  grid-column: 1/3;
  display: flex;
  align-items: center;
  margin-top: 9px;
  box-sizing: border-box;
}

.form-step__button {
  position: absolute;
  padding: 0;
  top: 0;
  right: 0;
  background-color: transparent;
  border: none;
  display: block;
  width: 40px;
  height: 100%;
  cursor: pointer;
  outline: none;

  &::after,
  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 14px;
    height: 2px;
    background-color: hsla(0, 0%, 80%, 1);
    content: "";
  }

  &::before {
    transform: rotate(90deg);
  }

  &:hover::before,
  &:hover::after {
    background-color: hsla(0, 0%, 60%, 1);
  }

  &:active::before,
  &:active::after,
  &:focus-visible::before,
  &:focus-visible::after {
    background-color: hsla(0, 0%, 90%, 1);
  }
}

.form-step__button-reduce {
  left: 0;

  &::before {
    display: none;
  }
}

.form-step__field {
  flex-grow: 1;
  width: 100%;
  font-family: "Blogger Sans", "Arial", sans-serif;
  text-align: center;
  box-sizing: border-box;
  height: 43px;
  outline: none;
  border: 1px solid hsla(227, 16%, 82%, 1);
  border-radius: 4px;
  color: hsla(224, 52%, 24%, 1);
  font-size: 14.86px;
  line-height: 18px;
  padding-left: 38px;
  padding-right: 38px;

  &::placeholder {
    font-family: "Blogger Sans", "Arial", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 1;
    color: hsla(224, 52%, 24%, 1);
  }

  &:hover {
    border-color: hsla(228, 16%, 64%, 1);
  }

  &:focus-visible,
  &:active {
    border-color: hsla(228, 41%, 15%, 1);
  }
}

.form-step__field:hover ~ .form-step__button::after,
.form-step__field:hover ~ .form-step__button::before {
  background-color: hsla(228, 16%, 64%, 1);
}

.form-step__field:focus-visible ~ .form-step__button::after,
.form-step__field:active ~ .form-step__button::after,
.form-step__field:focus-visible ~ .form-step__button::before,
.form-step__field:active ~ .form-step__button::before {
  background-color: hsla(228, 41%, 15%, 1);
}

.form-step__small {
  color: hsla(224, 52%, 24%, 0.3);
  text-transform: uppercase;
  letter-spacing: 0.07em;
  font-size: 16px;
  line-height: 1;
  font-weight: 500;
  text-align: end;
}

.form-step__field-group-child {
  position: relative;
}

.form-step__label-child {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.form-step__label-child:active .form-step__mark {
  background-color: hsla(228, 35%, 92%, 1);
  border-color: hsla(228, 35%, 92%, 1);
}

.form-step__mark {
  width: 24px;
  height: 24px;
  border-radius: 4px;
  border: 1px solid hsla(227, 16%, 82%, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 13px;
}

.form-step__checkbox-icon-child {
  display: none;
  width: 8px;
  fill: hsla(228, 41%, 15%, 1);
  height: auto;
}

.form-step__input[type="checkbox"]:checked + .form-step__label-child .form-step__checkbox-icon-child {
  display: block;
}

.form-step__input[type="checkbox"]:hover + .form-step__label-child .form-step__mark {
  border-color: hsla(228, 16%, 64%, 1);
}


.form-step__input[type="checkbox"]:active + .form-step__label-child .form-step__mark {
  background-color: hsla(228, 35%, 92%, 1);
  border-color: hsla(228, 35%, 92%, 1);
}

.form-step__input[type="checkbox"]:focus-visible + .form-step__label-child .form-step__mark {
  background-color: hsla(228, 35%, 92%, 1);
  border-color: hsla(228, 35%, 92%, 1);
}

.form-step--last {
  margin-bottom: 0;
}

.form-step__field-group--active-select {
  border: none;
  background-color: hsla(224, 52%, 24%, 1);
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  margin-right: 2px;
}

.form-step__field--active-select-second {
  color: $white;
  cursor: pointer;
  padding-left: 17px;
  padding-top: 16px;
  letter-spacing: 0.02em;
  height: 40px;
  border-top-left-radius: 4px;

  &::placeholder {
    color: $white;
    letter-spacing: 0.02em;
  }
}

@media (min-width: $tablet-width) {
  .form-step {
    padding: 0;
    margin-bottom: 34px;
  }

  .form-step__label {
    font-size: 20px;
    line-height: 18px;
    padding-top: 25px;
    padding-bottom: 26px;
    padding-right: 20px;
    letter-spacing: 0.1em;
  }

  .form-step__small {
    font-size: 20px;
    line-height: 18px;
    letter-spacing: 0.08em;
    align-self: center;
    padding-right: 5px;
  }

  .form-step__field-wrapper {
    margin: 0;
    padding: 0;
  }

  .form-step__field {
    height: 69px;
    font-size: 20px;
    line-height: 1;
    padding-left: 66px;
    padding-right: 73px;

    &::placeholder {
      font-size: 20px;
      line-height: 1;
    }
  }

  .form-step__button {
    width: 66px;

    &::after,
    &::before {
      width: 20px;
    }
  }

  .form-step__field-group:first-of-type {
    margin-bottom: 26px;
  }

  .form-step__field-group--duration {
    order: 1;
    margin-bottom: 26px;
  }

  .form-step__field-group--active-select {
    background-color: transparent;
    margin-right: 0;
  }

  .form-step__small--duration {
    padding: 0;
    margin-right: -3px;
  }

  .form-step__label-desc {
    font-size: 20px;
    letter-spacing: 0.1em;
    margin: 0;
  }

  .form-step__mark {
    width: 34px;
    height: 34px;
    margin-right: 22px;
  }

  .form-step__field-group-child {
    min-height: 35px;
    padding-bottom: 25px;

    &::before {
      content: "";
      position: absolute;
      bottom: -3px;
      right: 0;
      left: 0;
      height: 1px;
      background-color: hsla(225, 15%, 90%, 1);
    }
  }

  .form-step__label-child {
    margin: 0;
    font-size: 20px;
    line-height: 1;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-weight: 500;
  }

  .form-step__checkbox-icon-child {
    width: 11px;
  }
}

@media (min-width: $desktop-width) {
  .form-step {
    display: grid;
    grid-template-columns: 1fr auto;
    margin-bottom: 0;
    padding-top: 31px;
    padding-left: 1px;
  }

  .form-step__field-group {
    grid-column: 1/2;
    grid-template-columns: 221px 180px 35px min-content;

    &:first-of-type {
      margin-bottom: 0;
    }
  }

  .form-step__field {
    height: 70px;
    padding-right: 69px;
    padding-bottom: 5px;
  }

  .form-step__button {
    width: 59px;
  }

  .form-step__small {
    grid-column: 4/5;
    letter-spacing: 0;
  }

  .form-step__field-group--duration {
    margin-bottom: 0;
  }

  .form-step__field-group-child {
    grid-column: 2/3;
    grid-row: 1/2;
    padding: 0;
    margin-top: 17px;

    &::before {
      display: none;
    }
  }

  .form-step__checkbox-icon-child {
    width: 13px;
  }

  .form-step__label {
    letter-spacing: normal;
  }

  .form-step__label-desc {
    margin-bottom: 2px;
  }

  .form-step__field-wrapper {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
